<template>
  <div ref="trys"  style="height: 100%;">
    <div v-if="showInfo" class="info-container" style= "margin-top:0;height: 100%;">
      <info :option="rowOption" @closeDiv="closeDiv" ref="infoDetail"></info>
    </div>
    <div v-show="!showInfo"  class="all-container" >
      <div class="tabs-show" >
        <selects  :option="selectOpt"  @getSelect="getOption"></selects>
      </div>
      <div class="detailInfo" style="height:35%;margin-top:-0.5em" ref="lineContent" >
        <div class="h5-headers">   <span style="line-height:1">召测情况分析</span></div>
        <div id="lineCanvas" class="lineDiv" :style="{width: '100%', height: 'calc(100% - 2em)'}"></div>
      </div>
      <div class="detailInfo" style="height:calc(64% - 3.5em);margin-top:0.5em" ref="tableContainer">
        <div class="h5-headers">
          <span style="line-height:1">冻结数据详情</span>
          <button class="list-operate " style="float:right" @click="getAllData('freezeAnalyze/sumFreeForDay', true)"><i class="hasIcon"><icon style="font-size:0.84em" name="refresh"></icon></i>刷新</button>
        </div>
        <div ref="myOwnTable" class="table-container noBorderTable" style="height:calc(100% - 2.3em)">
          <tables v-if="showTable" :tableOpt="tableOption"  ref="myTable" @rowDbClick="clickTableRow"></tables>
        </div>
      </div>
    </div>
    <dialogs type="loading" ref="loading"></dialogs>
    <dialogs type="alert" ref="alert"></dialogs>
  </div>
</template>
<script>
//  import echarts from 'echarts'
  import info from './Info.vue'
  export default {
    data () {
      return {
        deviceType: '07',
        rowOption: '',
        showInfo: false,
        url: '',
        controls: [],
        showTable: false,
        tableOption: {
          rowName: 'data',
          column: [
            {title: '抄表时间', field: 'collectDate', textAlign: 'center'},
            {title: '召测户数', field: 'customerCount', textAlign: 'center'},
            {
              title: '成功数',
              field: 'successCount',
              textAlign: 'center',
              type: 'object',
              formatter: function (val, index, row, obj) {
                return {
                  template: '<span  @click="getDetail" style="padding: 0 1.2em;color:#3DD183">' + val + '</span>',
                  methods: {
                    getDetail: function () {
                      let parent = obj.$parent;
                      parent.rowClick(row, 1, 0)
                    }
                  }
                }
              }
            },
            {
              title: '失败总数',
              field: 'failCount',
              textAlign: 'center',
              type: 'object',
              formatter: function (val, index, row, obj) {
                return {
                  template: '<span @click="getDetail" style="padding: 0 1.2em;color:#CB565C">' + val + '</span>',
                  methods: {
                    getDetail: function () {
                      let parent = obj.$parent;
                      parent.rowClick(row, 0)
                    }
                  }
                }
              }
            },
            {
              title: '成功率(%)',
              field: 'successRate',
              textAlign: 'center',
              type: 'string',
              formatter: function (val) {
                return  parseFloat(val).toFixed(2) + '%'
              }
            },
            {
              title: '更新时间',
              field: 'updateTime',
              textAlign: 'center',
              type: 'string',
              formatter: function (val) {
                return val || '--'
              }
            }
          ],
          data: [],
          pageSize: 20,
          pageNumber: 1,
          pageList: [20, 40, 60],
          queryParam: {deviceType: '07'},
          isExport: true,
          noNormalData: true,
          exportURL: 'freezeAnalyze/downloadFrozenDataDetail',
          noExportCurrent: true
        },
        line: '',
        lineWidth: '100%',
        lineHeight: 'calc(100% - 2.1em)'
      }
    },
    methods: {
      //详情返回后的触发操作
      closeDiv: function () {
        this.showInfo = false;
        let that = this;
        that.$nextTick(function () {
          that.line.resize();
          that.$refs.myTable.onResize();
        });

      },
      //表格单击事件
      clickTableRow: function (item) {
        this.rowClick(item.val, 1)
      },
      //实现折线图，与表格为同一数据源，z,y分别代表对应坐标轴的数据
      makeLine: function (x, y) {
        this.line = echarts.init(document.getElementById('lineCanvas'));
        let option = {
          legend: {
            show: false,
            itemWidth: 55,
            padding: [5, 20, 15, 10],
            x: 'right',
            top: 20,
            textStyle: {
              color: '#000'
            }
          },
         /*  tooltip: {
                          trigger: 'axis',
                          position: function (pos, params, dom, rect, size) {
                            let width = dom.clientWidth;
                            if(size.viewSize[0] - pos[0] < width) {
                             return [pos[0] - width, '10%'];
                           }
                           return [pos[0], '10%'];
                         }
                       },*/
          toolbox: {
            show: false,
            feature: {
              dataZoom: {
                yAxisIndex: 'none'
              },
              restore: {},
              saveAsImage: {}
            }
          },
          grid: {
            left: 60,
            right: 30,
            top: 10,
            bottom: 40
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: x,
            axisLabel: {
              textStyle: {
                color: '#fff'
              },
              formatter: function (value, index) {
                if (value.length < 5) {
                  return value
                }
                return value.substr(5)
              }
            },
            axisTick: {
              show: true,
              lineStyle: {
                color: '#fff'
              }
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#fff'
              }
            }
          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, '100%'],
            max: 100,
            min: 0,
            axisLabel: {
              formatter: '{value} %',
              textStyle: {
                color: '#fff'
              }
            },
            axisTick: {
              show: false
            },
            axisLine: {
              show: false
            }
          },
          tooltip: {
            trigger: 'axis',
            borderRadius: 20,
            backgroundColor: 'rgba(202,79,82, 0.5)',
            padding: [10, 8],
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            },
            formatter: function (params) {
              return params[0].data + '%';
            },
            textStyle: {
              color: '#fff',
              fontWeight: 600,
              fontSize: 16
            }
          },
          series: {
            name: '',
            type: 'line',
            smooth: false,
           // sampling: 'average',
            itemStyle: {
              normal: {
                color: '#CA4F52'
              }
            },
            areaStyle: {
              normal: {
                color: '#544252'
              }
            },
            symbol: 'circle',
            symbolSize: 10,
            data: y
          }
        };
        this.line.setOption(option);
      },
      //失败数和成功数列点击之后的操作，与行单击事件一致，type参数代表详情页显示成功或失败列表
      rowClick: function (row, type) {
        this.rowOption = {
          row: row,
          type: type,
          deviceType: this.deviceType
        };
        this.showInfo = true;
      },
      //获取折线和表格数据
      getAllData: function (url, showLoading) {
        let that = this;
        let loading = this.$refs.loading;
        if (showLoading) {
          loading.show({
            msg: '正在加载数据，请稍后'
          })
        }
        that.getRequest({
//                    url: 'dist/json/analyse.json',
//                    method: 'get',
          url: url,
          method: 'post',
          param: {deviceType: that.deviceType},
          success: function(data) {
            loading.close();
            if(showLoading) {
              that.$refs.alert.show({
                msg: data.message
              })
            }
            if (!that.showTable) {
              that.tableOption.data = data.data;
              that.tableOption.height = that.$refs.tableContainer.offsetHeight - 110;
              that.showTable = true;
            } else {
              that.$refs.myTable.resetTableOpt({
                data: data.data,
                queryParam: {deviceType: that.deviceType}
              })
            }
            let lineX = [];
            let lineY = [];
            for (let i=(data.data.length - 1); i >= 0; i--) {
              lineX.push(data.data[i].collectDate);
              lineY.push(parseFloat(data.data[i].successRate).toFixed(2));
            }
            that.makeLine(lineX, lineY);
          },
          error: function (data) {
            if(showLoading) {
              that.$refs.alert.show({
                msg: data.message
              })
            }
          }
        }, true)
      },
      init: function () {
        let that = this;
        that.tableOption.isExport = that.controls[6];
        this.getAllData('freezeAnalyze/rateChart', false);
       /* window.onresize = function () {
          that.windowChange();
        }*/
      },
      resizeCon: function () {
        let that = this;
        that.line !== '' ? that.line.resize() : '';
        if ( that.$refs.myTable) {
          that.$refs.myTable.onResize();
        }
        if (that.$refs.infoDetail) {
          let info = that.$refs.infoDetail;
          if (info.$refs.myTable){
            info.$refs.myTable.resizeTable(info.$refs.myOwnTable.width, info.$refs.myOwnTable.clientHeight - 50);
            info.line !== ''?  info.line.resize() : '';
            info.pie !== '' ? info.pie.resize() : '';
          }
        }
      },
      windowChange: function () {
        let that = this;
        that.resizeCon();
        /*window.onresize = function () {
          that.resizeCon();
        }*/
      }
    },
    components: {
      info
    },
    computed: {
      selectOpt() {
        return {
          cs: 'client',
          width: '7em',
          key: 'deviceType',
          //data: this.getEnumItem('deviceType').obj
          data: [{text: '电表', value: '07'}]
        }
      }
    },
    mounted: function () {
      this.isIn();
      this.getAction();
    },
    watch: {
      'deviceType': {
        handler(val, oldVal) {
          if (val !== oldVal) {
            this.getAllData('freezeAnalyze/rateChart', false);
          }
        }
      }
    }
  }
</script>
<style scope lang="scss">
  $headerColors: transparent;
  .tabs-show{
    margin: 0.8em 0em 0 0;
    float:right;
  }
  .h5-headers{
    background: $headerColors;
    text-align: left;
    color:white;
    padding: 0 1.4em;
    margin-bottom:0.6em;
    height: 2em;
    line-height: 2em;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size:0.94em
  }
  .detailInfo{
    width: 100%;
    background: transparent;
    margin:0;
    float:left
  }
  .lineDiv{
    width: 100%;
    //  height: calc(100% - 2em);
    height: 14em
  }
</style>



