//                    method: 'get',
//                    url: 'dist/json/analysePie.json',
                    param: {jzqNo: '999999999', freezeDate: that.row.collectDate, deviceType: that.option.deviceType},
                    success: function (data) {
                        let pieChart = [];
                        let legend = [];
                        if (data.data && data.data.length > 0) {
                            if (that.pie !== null && that.pie !== "" && that.pie !== undefined) {
                                that.pie.dispose();
                            }
                            that.pie = echarts.init(document.getElementById('pieCharts'));
                            for (let i = 0; i < data.data.length; i++) {
                                let item = data.data[i];
                                pieChart.push({value: item['meterCnt'], name: item['label']});
                                legend.push(item['label']);
                            }
                            option.series[1].data = pieChart;
                            option.legend.data = legend;
                            that.pie.setOption(option);
                        } else {
                            that.showErrorP = true;
                        }
                    },
                  error: function () {
                    that.showErrorP = true;
                  }
                }, true)
            },
            init: function () {
               
               this.row = this.option.row;
               this.index = this.option.type === 0 ? 1: 0;
               this.btnOpt[0].num = this.row.successCount;
               this.btnOpt[1].num = this.row.failCount;
               this.tableOption.queryParam = {flag:this.option.type, date: this.row.collectDate, deviceType: this.option.deviceType};
               this.tableOption.column = this.columns[this.option.type];
               this.tableOption.height = this.$refs.myOwnTable.clientHeight - 80;
               this.tableShow = true;
               this.makeLine();
               this.makePie();
                let that = this;
                that.getRequest({
                    url: 'freezeAnalyze/rateGroupByCjq',
                    method: 'post',
//                    method: 'get',
//                    url: 'dist/json/analyseSuccess.json',
                    param: {freezeDate: that.row.collectDate, deviceType: that.option.deviceType, pageSize: 20, pageNumber: 1, sortName: 'failCount',sortOrder: 'desc'},
                    success: function (data) {
                        that.btnOpt[2].num = data.total || 0
                    }
                });
            },
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            }
        },
        mounted: function () {
         // console.log(this.option)
            this.isIn();
            this.init();
        }
    }
</script>
<style lang="scss">
    $headerColor:#44BBDD;
    .info-container{
        margin-top:5px
    }
    .lineCharts{
        width: 70%;
        height: 100%;
        display: inline-block;
        float:left;
        position:relative;
        //border-right: 1px solid rgba(255,255,255,0.15)
    }
    .lineCharts:after{
      content: '';
      height:84%;
      width:1px;
      background:  rgba(255,255,255,0.15);
      position:absolute;
      right:0;
      top: 8%
    }
    .pieCharts{
        width: calc(30% - 2px);
        height: 100%;
        display: inline-block;
        float:left;
    }

</style>



